<template>
  <div class="sb">
    <div class="secondBox">
      <!-- <button @click="qingqiu">请求</button> -->
      <div v-for="(item, sid) in dataItem" :key="sid">
        <div v-if="page == item.sid">
          <table v-show="istrue">
            <thead>
              <tr>
                <th class="tf">id</th>
                <th class="ts">名称</th>
                <th class="tt">当前状态</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td class="tf">{{ item.sid }}</td>
                <td class="ts">{{ item.name }}</td>
                <td class="tt">{{ item.status }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="sc-buttom-box">
        <div class="sc-buttom-box-s">
          <span>共 {{ dataItem.length }} 页</span>
        </div>
        <div class="sc-buttom-box-f">
          <i class="fas fa-angle-left" @click="jian"></i>
          <span>第 {{ this.page }} 页</span>
          <i class="fas fa-angle-right" @click="jia"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import instance from "@/js/axios";
export default {
  name: "Listdata",
  data() {
    return {
      dataItem: "",
      page: 1,
      istrue:false,
    };
  },
  methods: {
    qingqiu() {
      instance.get("/api/gdupt/bearing/page?page=1&pageSize=3").then(
        (response) => {
          // console.log("成功", response.data.reMsg);
          this.dataItem = response.data.reMsg;
          this.istrue=true
        },
        (error) => {
          console.log("请求失败了", error.message);
        }
      );
    },
    jian() {
      if (this.page != 1) {
        this.page--;
      }
    },
    jia() {
      if (this.page < this.dataItem.length) {
        this.page++;
      }
    },
  },
  created() {
    this.qingqiu();
  },
};
</script>

<style>
.sb {
  position: relative;
  height: 100%;
}
.sb .secondBox {
  color: azure;
}
.sb .sc-buttom-box {
  position: absolute;
  width: 100%;
  height: 30px;
  border: 1px solid rgba(53, 190, 214, 0.2);
  bottom: 0;
  font-size: 10px;
}
.sb .sc-buttom-box span {
  /* display: block;
  float: right;
  margin-top: 2px; */
  vertical-align: middle;
}
.sc-buttom-box-f,
.sc-buttom-box-s {
  float: right;
  font-size: 15px;
}
.sc-buttom-box-s {
  margin: 2px 10px auto 10px;
}
.sc-buttom-box-f{
  display: flex;
}
.sc-buttom-box-f span{
  display: block;
  margin: 4px 0 0 0;
}
.sc-buttom-box-f i {
  margin: 5px 5px auto 5px;
  font-size: 20px;
}
</style>